import {Get_Dev_Layer} from "../js";
import {Draw_Dev_Icon_Marker, Draw_Warning_RoundMarker,
        Draw_Warning_Marker_Line,Draw_Target_SectorMarker} from "./mapDraw";
import {RADAR_IMG, PINPU_IMG, GUANGDIAN_IMG, ATTACK_IMG, DECOY_IMG,
        RADAR_SELECT_IMG, PINPU_SELECT_IMG, GUANGDIAN_SELECT_IMG, ATTACK_SELECT_IMG, DECOY_SELECT_IMG, }
        from "../mapHandle/mapImgSource"
import {featureType_enum} from "../mapHandle/mapDraw";

let dev4offet = [];
// 画出图标
export function Draw_DevIcon_Marker(devList)
{
  if (null == devList)
  {
    return;
  }
	if(dev4offet.length<devList.length){

		let devoffsetX = -60;
		for(let i = 0; i < devList.length; i++){
			devList[i].offestX = devoffsetX;
			devoffsetX = devoffsetX + 60;
		}
		dev4offet = devList;
	}
	else{
		for(let i = 0; i < devList.length; i++){
			let devinfo = dev4offet.filter(inf=>{
				if(inf.id == devList[i].id){
					return inf;
				}
			});
			if(devinfo!= undefined&&devinfo!=null&&devinfo.length!=0){
				devList[i].offestX = devinfo[0].offestX;
			}
		}
	}
  devList.forEach(info => {
    const {
      name, display, category, ip, port,
      lip, lport, lat, lng, alt,
      coverR, coverS, coverE, id,
      rectifyAZ, rectifyEl,
      threadAssessmentCount,
      targetTimeOut,
      statusReportingInterval,
      probeReportingInterval
    } = info;

    let feature_ = Get_Dev_Layer().getSource().getFeatureById(id);

    if (null == feature_)
    {
      if(category!=null)
      {
        if(category<=20000)
        {
          if(category>10200)
          {
            info.devImg = PINPU_IMG+'_level0.png';
            info.devSelectImg = PINPU_SELECT_IMG+'_level0.png';
          }else{
            info.devImg = RADAR_IMG+'_level0.png';
            info.devSelectImg = RADAR_SELECT_IMG+'_level0.png';
          }
        }
        else if(category>20000 && category<=30000)
        {
          info.devImg = GUANGDIAN_IMG+'_level0.png';
          info.devSelectImg = GUANGDIAN_SELECT_IMG+'_level0.png';
        }
        else if(category>30000 && category<=30500)
        {
          info.devImg = ATTACK_IMG+'_level0.png';
          info.devSelectImg = ATTACK_SELECT_IMG+'_level0.png';
        }
        else if(category>30500)
        {
          info.devImg = DECOY_IMG+'_level0.png';
          info.devSelectImg = DECOY_SELECT_IMG+'_level0.png';
        }else
          {
          info.devImg = RADAR_IMG+'_level0.png';
          info.devSelectImg = RADAR_SELECT_IMG+'_level0.png';
        }
        // 主中心图标位置
        Draw_Dev_Icon_Marker(name, {lat: lat, lng: lng},info.devImg,info,featureType_enum.dev);
        // 告警等距线
        Draw_Warning_Marker_Line({lat: lat, lng: lng}, coverR,info.id);
        // 告警区域范围
        Draw_Warning_RoundMarker({lat: lat, lng: lng}, coverR,info.id,coverS,coverE);
      }
    }
  })
}



